import './UserStatistic.scss'

export default function UserStatistic({ experience }){

  const liga = () =>{
    let ligaName
    if(experience <= 500){
      ligaName = ''
    }else if(experience >= 1000){
      ligaName = ""
    }else if(experience >= 2000){
      ligaName = ''
    }
    return ligaName
  }

  const ligs = liga()

  return(
    <div className='user-statistics'>
      <h2 className='user__block-title'></h2>

      <ul className='statistic-list'>
        <li className='statistic-item inactive'>
          <p className='statistic-count'>0</p>
          <h3 className='statistic-name'> </h3>
        </li>
        <li className='statistic-item'>
          <p className='statistic-count'>{experience}</p>
          <h3 className='statistic-name'> </h3>
        </li>
        <li className='statistic-item'>
          <p className='statistic-count'>{experience === 0 ? ' ' : ligs}</p>
          <h3 className='statistic-name'> </h3>
        </li>
        <li className='statistic-item inactive'>
          <p className='statistic-count'>0</p>
          <h3 className='statistic-name'>-3</h3>
        </li>
      </ul>
    </div>
  )
}